/** STYLES */
/* Global variables */
/*imports*/
.app-bar {
  position: fixed;
  z-index: 2;
  color: white;
  width: 100%;
  height: 8vh;
  background: #77c4f6; }
  .app-bar .bar {
    display: flex;
    height: 100%;
    align-items: center; }
    .app-bar .bar .logo {
      cursor: pointer; }
    .app-bar .bar .nav-box {
      text-transform: uppercase;
      margin-left: auto;
      display: flex;
      justify-content: space-between; }
      .app-bar .bar .nav-box .nav-item {
        cursor: pointer;
        padding: 0 1em; }
        .app-bar .bar .nav-box .nav-item:hover {
          color: #cef6ff; }

@media screen and (max-width: 768px) {
  .app-bar {
    position: fixed; }
  .hamburger {
    position: fixed;
    cursor: pointer;
    right: 5%;
    z-index: 2; }
  .line {
    width: 30px;
    height: 3px;
    background: white;
    margin: 5px; }
  .nav-box {
    position: fixed;
    text-align: center;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    justify-content: flex-start !important;
    background: #77c4f6;
    flex-direction: column;
    clip-path: circle(100px at 90% -20%);
    -webkit-clip-path: circle(100px at 90% -20%);
    transition: all 1s ease-out;
    pointer-events: none; }
    .nav-box .nav-item {
      padding: 2em !important;
      font-size: 2em; }
    .nav-box.open {
      pointer-events: all;
      clip-path: circle(1000px at 90% -20%);
      -webkit-clip-path: circle(1000px at 90% -20%); } }

.icon-bg {
  width: 8em;
  height: 8em;
  background: #77c4f6;
  border-radius: 50%;
  margin: auto; }
  .icon-bg .icon {
    color: #354458;
    text-align: center;
    padding-top: 25%; }
    .icon-bg .icon .MuiSvgIcon-root {
      font-size: 4em; }

.btn, .btn-selected {
  font-size: 1em;
  text-transform: uppercase;
  padding: 0.5em 1em;
  border: 3px solid white;
  background: rgba(53, 68, 88, 0.8);
  color: white; }
  .btn:hover, .btn-selected:hover {
    background: #354458;
    cursor: pointer; }
  .btn:focus, .btn-selected:focus {
    background: #77c4f6; }

.btn-selected {
  background: #77c4f6; }

.gallery-container {
  margin: 50px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex: 0 0 auto; }
  .gallery-container .gallery-img {
    width: 22vw;
