    background-image: url("https://i.imgur.com/8Ov8StI.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 1px; }

.slider-wrapper {
  display: flex; }

button {
  background: transparent;
  border: none;
  outline: none;
  color: #77c4f6; }
  button .MuiSvgIcon-root {
    font-size: 4rem; }

.slide-item {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  height: 360px;
  width: 25%; }

.contact {
  display: flex;
  width: 80%;
  margin: auto; }

.headings {
  margin-top: 3.67em; }

.landing-image {
